.switcher {
	position: relative;

	&__check {
		position: absolute;
		display: block;
		height: 0;
		width: 0;
		opacity: 0;
	}

	&__body {
		position: relative;
		display: block;
		height: 2.5em;
		width: 11em;
		border-radius: 2em;
		background-color: var(--color__switcher-bg);
		box-shadow: var(--shadow-3);
		border: 2px solid var(--color__switcher-border);
		margin: 0;
		padding: 0;
	}

	&__move {
		--bg: var(--grad-1);
		position: absolute;
		display: block;
		width: 6.25em;
		height: 100%;
		border-radius: 2em;
		background: var(--bg);
		top: 0;
		left: 4.5em;
		transition: all 0.24s;
	}

	&__text {
		opacity: 1;
		position: absolute;
		@include centering('');
		text-transform: uppercase;
		font-weight: 700;
		white-space: nowrap;
		padding: 4px 2px;
		color: var(--color__white);
		transition: all 0.24s;

		&:last-child {
			opacity: 0;
		}
	}

	&__check:checked+&__body {
		.switcher__move {
			--bg: var(--grad-3);
			width: 5.5em;
			left: 0;
		}

		.switcher__text {
			opacity: 0;

			&:last-child {
				opacity: 1;
			}
		}
	}

}